nav{
  padding: 1rem;
  border-bottom: 3px solid #000;
}
nav ul{
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
}
nav ul li{
  margin: 0 2rem;
}

nav a{
  color:#000;
  font-weight: bolder;
  font-size: 1.125rem;
  text-decoration: none;
}
nav a:hover{
  color:indianred;
}
.page-body{
  width:90%;
  max-width: 1200px;
  margin: 3rem auto;
}
p{
  color:rgb(75, 75, 75);
  line-height: 1.85;
}
.article{
  margin-bottom: 2rem;
  border-left: 3px solid indianred;
  padding-left: .75rem;
}
.article-link{
  display: block;
  margin-bottom: 1rem;
  font-size: 1.35rem;
  color: #000;
  text-decoration: none;
}
.article-link:hover{
  color:indianred;
}
.upvotes-section{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
button{
  margin-right: .5rem;
  padding: .9rem 1rem;
  border:none;
  color: #fff;
  background-color: indianred;
}
button:hover,
button:focus{
  background-color: #a84848;
  cursor: pointer;
}
.upvotes{
  padding: .5rem 1rem;
  background-color:lemonchiffon;
}
.comment{
  margin-bottom: 1rem;
  background-color: lemonchiffon;
}
.comment h4,
.comment p{
  padding: .5rem 1rem;
}
.comment h4{
  margin-bottom: 0;
  background-color: #e6e2c0;
}
.add-comment{
  margin:3rem 0;
}
.add-comment label{
  display: block;
  margin-bottom: 1rem;
}
.add-comment input,
.add-comment textarea{
  display: block;
  margin:.5rem 0;
  border-radius: .35rem;
}